// __tests__/useActivityMetrics.test.js

import { renderHook, act } from '@testing-library/react-native';
import AsyncStorage from '@react-native-async-storage/async-storage';
import useActivityMetrics from '../src/hooks/useActivityMetrics';

// Mock AsyncStorage
jest.mock('@react-native-async-storage/async-storage', () => ({
  getItem: jest.fn(),
  setItem: jest.fn(),
}));

// Mock Date pour avoir un contrÃ´le sur les dates
const mockDate = new Date('2024-01-15T10:00:00.000Z');
const originalDate = global.Date;

describe('useActivityMetrics', () => {
  beforeEach(() => {
    // Reset tous les mocks
    jest.clearAllMocks();
    AsyncStorage.getItem.mockResolvedValue(null);
    AsyncStorage.setItem.mockResolvedValue();
    
    // Mock Date.now et Date constructor
    global.Date = jest.fn(() => mockDate);
    global.Date.now = jest.fn(() => mockDate.getTime());
    Object.setPrototypeOf(global.Date, originalDate);
    Object.getOwnPropertyNames(originalDate).forEach(name => {
      if (name !== 'constructor') {
        global.Date[name] = originalDate[name];
      }
    });
  });

  afterEach(() => {
    global.Date = originalDate;
  });

  describe('Initialisation', () => {
    test('doit initialiser avec des valeurs par dÃ©faut quand AsyncStorage est vide', async () => {
      AsyncStorage.getItem.mockResolvedValue(null);

      let result;
      await act(async () => {
        const hookResult = renderHook(() => useActivityMetrics());
        result = hookResult.result;
        // Attendre un cycle pour que useEffect se dÃ©clenche
        await new Promise(resolve => setTimeout(resolve, 0));
      });

      expect(result.current.currentStreak).toBe(0);
      expect(result.current.todayMinutes).toBe(0);
      expect(result.current.formattedTime).toBe('0min');
      expect(result.current.streakTrend).toBeNull();
    });

    test('doit charger les donnÃ©es existantes du mÃªme jour', async () => {
      const today = mockDate.toDateString();
      
      AsyncStorage.getItem.mockImplementation((key) => {
        switch (key) {
          case 'current_streak': return Promise.resolve('5');
          case 'today_minutes': return Promise.resolve('45');
          case 'last_time_date': return Promise.resolve(today);
          default: return Promise.resolve(null);
        }
      });

      let result;
      await act(async () => {
        const hookResult = renderHook(() => useActivityMetrics());
        result = hookResult.result;
        await new Promise(resolve => setTimeout(resolve, 0));
      });

      expect(result.current.currentStreak).toBe(5);
      expect(result.current.todayMinutes).toBe(45);
      expect(result.current.formattedTime).toBe('45min');
    });

    test('doit rÃ©initialiser todayMinutes pour un nouveau jour', async () => {
      const yesterday = new Date(mockDate);
      yesterday.setDate(yesterday.getDate() - 1);
      
      AsyncStorage.getItem.mockImplementation((key) => {
        switch (key) {
          case 'current_streak': return Promise.resolve('5');
          case 'today_minutes': return Promise.resolve('45');
          case 'last_time_date': return Promise.resolve(yesterday.toDateString());
          default: return Promise.resolve(null);
        }
      });

      let result;
      await act(async () => {
        const hookResult = renderHook(() => useActivityMetrics());
        result = hookResult.result;
        await new Promise(resolve => setTimeout(resolve, 0));
      });

      expect(result.current.currentStreak).toBe(5);
      expect(result.current.todayMinutes).toBe(0);
      expect(AsyncStorage.setItem).toHaveBeenCalledWith('today_minutes', '0');
      expect(AsyncStorage.setItem).toHaveBeenCalledWith('last_time_date', mockDate.toDateString());
    });
  });

  describe('Session Timer', () => {
    test('doit dÃ©marrer et arrÃªter une session correctement', async () => {
      let result;
      await act(async () => {
        const hookResult = renderHook(() => useActivityMetrics());
        result = hookResult.result;
        await new Promise(resolve => setTimeout(resolve, 0));
      });

      // DÃ©marrer la session
      act(() => {
        result.current.startSession('meditation');
      });

      // Simuler 5 minutes plus tard
      const sessionEndTime = mockDate.getTime() + (5 * 60 * 1000);
      global.Date.now = jest.fn(() => sessionEndTime);

      // ArrÃªter la session
      await act(async () => {
        await result.current.endSession();
      });

      expect(result.current.todayMinutes).toBe(5);
      expect(AsyncStorage.setItem).toHaveBeenCalledWith('today_minutes', '5');
      expect(AsyncStorage.setItem).toHaveBeenCalledWith('last_time_date', mockDate.toDateString());
    });

    test('ne doit pas mettre Ã  jour si la session est trop courte (< 1 minute)', async () => {
      // RÃ©initialiser les mocks pour ce test spÃ©cifique
      jest.clearAllMocks();
      AsyncStorage.getItem.mockResolvedValue(null);
      AsyncStorage.setItem.mockResolvedValue();

      let result;
      await act(async () => {
        const hookResult = renderHook(() => useActivityMetrics());
        result = hookResult.result;
        await new Promise(resolve => setTimeout(resolve, 0));
      });

      // DÃ©marrer la session
      act(() => {
        result.current.startSession();
      });

      // Simuler 30 secondes plus tard
      const sessionEndTime = mockDate.getTime() + (30 * 1000);
      global.Date.now = jest.fn(() => sessionEndTime);

      // ArrÃªter la session
      await act(async () => {
        await result.current.endSession();
      });

      expect(result.current.todayMinutes).toBe(0);
      // VÃ©rifier qu'aucune mise Ã  jour de session n'a Ã©tÃ© faite
      expect(AsyncStorage.setItem).not.toHaveBeenCalledWith('today_minutes', expect.stringMatching(/^[1-9]/));
    });

    test('ne doit rien faire si endSession est appelÃ© sans startSession', async () => {
      // RÃ©initialiser les mocks pour ce test spÃ©cifique
      jest.clearAllMocks();
      AsyncStorage.getItem.mockResolvedValue(null);
      AsyncStorage.setItem.mockResolvedValue();

      let result;
      await act(async () => {
        const hookResult = renderHook(() => useActivityMetrics());
        result = hookResult.result;
        await new Promise(resolve => setTimeout(resolve, 0));
      });

      await act(async () => {
        await result.current.endSession();
      });

      expect(result.current.todayMinutes).toBe(0);
      // Seules les initialisations devraient avoir appelÃ© setItem
      const setItemCalls = AsyncStorage.setItem.mock.calls;
      const sessionCalls = setItemCalls.filter(call => 
        call[0] === 'today_minutes' && parseInt(call[1]) > 0
      );
      expect(sessionCalls).toHaveLength(0);
    });

    test('doit accumuler les minutes de plusieurs sessions', async () => {
      // Initialiser avec 30 minutes existantes
      AsyncStorage.getItem.mockImplementation((key) => {
        switch (key) {
          case 'today_minutes': return Promise.resolve('30');
          case 'last_time_date': return Promise.resolve(mockDate.toDateString());
          default: return Promise.resolve('0');
        }
      });

      let result;
      await act(async () => {
        const hookResult = renderHook(() => useActivityMetrics());
        result = hookResult.result;
        await new Promise(resolve => setTimeout(resolve, 0));
      });

      expect(result.current.todayMinutes).toBe(30);

      // Nouvelle session de 10 minutes
      act(() => {
        result.current.startSession();
      });

      const sessionEndTime = mockDate.getTime() + (10 * 60 * 1000);
      global.Date.now = jest.fn(() => sessionEndTime);

      await act(async () => {
        await result.current.endSession();
      });

      expect(result.current.todayMinutes).toBe(40);
      expect(AsyncStorage.setItem).toHaveBeenCalledWith('today_minutes', '40');
    });
  });

  describe('Streak Logic', () => {
    test('doit commencer un nouveau streak', async () => {
      AsyncStorage.getItem.mockImplementation((key) => {
        if (key === 'last_activity_date') return Promise.resolve(null);
        return Promise.resolve('0');
      });

      let result;
      await act(async () => {
        const hookResult = renderHook(() => useActivityMetrics());
        result = hookResult.result;
        await new Promise(resolve => setTimeout(resolve, 0));
      });

      await act(async () => {
        await result.current.updateStreak();
      });

      expect(result.current.currentStreak).toBe(1);
      expect(AsyncStorage.setItem).toHaveBeenCalledWith('current_streak', '1');
      expect(AsyncStorage.setItem).toHaveBeenCalledWith('last_activity_date', mockDate.toDateString());
    });

    test('doit continuer le streak si activitÃ© hier', async () => {
      const yesterday = new Date(mockDate);
      yesterday.setDate(yesterday.getDate() - 1);

      AsyncStorage.getItem.mockImplementation((key) => {
        switch (key) {
          case 'current_streak': return Promise.resolve('3');
          case 'last_activity_date': return Promise.resolve(yesterday.toDateString());
          default: return Promise.resolve('0');
        }
      });

      let result;
      await act(async () => {
        const hookResult = renderHook(() => useActivityMetrics());
        result = hookResult.result;
        await new Promise(resolve => setTimeout(resolve, 0));
      });

      await act(async () => {
        await result.current.updateStreak();
      });

      expect(result.current.currentStreak).toBe(4);
      expect(AsyncStorage.setItem).toHaveBeenCalledWith('current_streak', '4');
    });

    test('doit redÃ©marrer le streak si gap > 1 jour', async () => {
      const twoDaysAgo = new Date(mockDate);
      twoDaysAgo.setDate(twoDaysAgo.getDate() - 2);

      AsyncStorage.getItem.mockImplementation((key) => {
        switch (key) {
          case 'current_streak': return Promise.resolve('5');
          case 'last_activity_date': return Promise.resolve(twoDaysAgo.toDateString());
          default: return Promise.resolve('0');
        }
      });

      let result;
      await act(async () => {
        const hookResult = renderHook(() => useActivityMetrics());
        result = hookResult.result;
        await new Promise(resolve => setTimeout(resolve, 0));
      });

      await act(async () => {
        await result.current.updateStreak();
      });

      expect(result.current.currentStreak).toBe(1);
      expect(AsyncStorage.setItem).toHaveBeenCalledWith('current_streak', '1');
    });

    test('ne doit pas mettre Ã  jour le streak si dÃ©jÃ  fait aujourd\'hui', async () => {
      const today = mockDate.toDateString();

      AsyncStorage.getItem.mockImplementation((key) => {
        switch (key) {
          case 'current_streak': return Promise.resolve('3');
          case 'last_activity_date': return Promise.resolve(today);
          default: return Promise.resolve('0');
        }
      });

      let result;
      await act(async () => {
        const hookResult = renderHook(() => useActivityMetrics());
        result = hookResult.result;
        await new Promise(resolve => setTimeout(resolve, 0));
      });

      const initialStreak = result.current.currentStreak;

      await act(async () => {
        await result.current.updateStreak();
      });

      expect(result.current.currentStreak).toBe(initialStreak);
      // Ne devrait pas appeler setItem pour current_streak aprÃ¨s l'initialisation
      const streakCalls = AsyncStorage.setItem.mock.calls.filter(call => call[0] === 'current_streak');
      expect(streakCalls).toHaveLength(0);
    });
  });

  describe('Getters et formatage', () => {
    test('getStreakTrend doit retourner les bons messages', async () => {
      const testCases = [
        { streak: 0, expected: null },
        { streak: 1, expected: 'ðŸ”¥ Continue!' },
        { streak: 3, expected: 'ðŸ’ª En forme!' },
        { streak: 7, expected: 'ðŸ† Incroyable!' },
        { streak: 10, expected: 'ðŸ† Incroyable!' }
      ];

      for (const testCase of testCases) {
        // Reset mocks pour chaque cas de test
        jest.clearAllMocks();
        AsyncStorage.getItem.mockImplementation((key) => {
          if (key === 'current_streak') return Promise.resolve(testCase.streak.toString());
          return Promise.resolve('0');
        });

        let result;
        await act(async () => {
          const hookResult = renderHook(() => useActivityMetrics());
          result = hookResult.result;
          await new Promise(resolve => setTimeout(resolve, 0));
        });

        expect(result.current.streakTrend).toBe(testCase.expected);
      }
    });

    test('getFormattedTime doit formater correctement', async () => {
      const testCases = [
        { minutes: 0, expected: '0min' },
        { minutes: 30, expected: '30min' },
        { minutes: 60, expected: '1h' },
        { minutes: 75, expected: '1h15min' },
        { minutes: 120, expected: '2h' },
        { minutes: 145, expected: '2h25min' }
      ];

      for (const testCase of testCases) {
        // Reset mocks pour chaque cas de test
        jest.clearAllMocks();
        AsyncStorage.getItem.mockImplementation((key) => {
          if (key === 'today_minutes') return Promise.resolve(testCase.minutes.toString());
          if (key === 'last_time_date') return Promise.resolve(mockDate.toDateString());
          return Promise.resolve('0');
        });

        let result;
        await act(async () => {
          const hookResult = renderHook(() => useActivityMetrics());
          result = hookResult.result;
          await new Promise(resolve => setTimeout(resolve, 0));
        });

        expect(result.current.formattedTime).toBe(testCase.expected);
      }
    });
  });

  describe('Gestion des erreurs', () => {
    test('doit gÃ©rer les erreurs AsyncStorage silencieusement', async () => {
      AsyncStorage.getItem.mockRejectedValue(new Error('Storage error'));

      let result;
      await act(async () => {
        const hookResult = renderHook(() => useActivityMetrics());
        result = hookResult.result;
        // Ne devrait pas throw
        await new Promise(resolve => setTimeout(resolve, 0));
      });

      expect(result.current.currentStreak).toBe(0);
      expect(result.current.todayMinutes).toBe(0);
    });

    test('doit gÃ©rer les erreurs lors de endSession', async () => {
      AsyncStorage.setItem.mockRejectedValue(new Error('Storage error'));

      let result;
      await act(async () => {
        const hookResult = renderHook(() => useActivityMetrics());
        result = hookResult.result;
        await new Promise(resolve => setTimeout(resolve, 0));
      });

      act(() => {
        result.current.startSession();
      });

      const sessionEndTime = mockDate.getTime() + (5 * 60 * 1000);
      global.Date.now = jest.fn(() => sessionEndTime);

      // Ne devrait pas throw
      await act(async () => {
        await result.current.endSession();
      });

      // Les valeurs locales devraient quand mÃªme Ãªtre mises Ã  jour
      expect(result.current.todayMinutes).toBe(5);
    });
  });
});